import uniStatusBar from "@/components/uni-status-bar/uni-status-bar"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniStatusBar,
		uniLoadMore
	},
	data(){
		return{
			active:1,
			rangeActive:'',
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	mounted(){
		var that=this
		this.getList(this.page)
		setTimeout(function(){
			that.$getHeight('#sv',(res) =>{
				that.sh=res
			})
		},0)
	},
	methods:{
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		toggleRange(t){
			console.log(t)
			this.rangeActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
			if(this.active!=3){
				this.rangeActive=''
			}else{
				this.rangeActive= this.rangeActive == 1 ? 2:1
			}
		},
		getList(p){
			var that=this
			var params={
			  page:p,
			  pagesize: this.pageSize
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='/wangtosale_list'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.allnum
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
	}
}
